import React, { useEffect, useState } from "react";
import { Typography, Slider, FormControl, FormLabel } from "@material-ui/core";
import { useNode } from "@craftjs/core";
import ContentEditable from 'react-contenteditable'

export const Text = ({text, fontSize, textAlign, tagName, placeholder, color}) => {
    const { connectors: {connect, drag}, selected, dragged, actions: {setProp} } = useNode((state) => ({
        selected: state.events.selected,
        dragged: state.events.dragged
      }));
    
    const [editable, setEditable] = useState(false);
    useEffect(() => {!selected && setEditable(false)}, [selected]);

    return (
        <div 
            ref={ref => connect(drag(ref))}
            onClick={e => setEditable(true)}
        >
        <ContentEditable 
            disabled={!editable}
            html={text} 
            onChange={e => 
                setProp(props => 
                  props.text = e.target.value.replace(/<\/?[^>]+(>|$)/g, "")  
                )
            }
            onKeyDown={(event) => {
              if (event.key === 'Enter') {
                document.execCommand('insertLineBreak')
                event.preventDefault()
              }
            }}
            tagName={tagName || "p"}
            style={{textAlign, whiteSpace: 'pre-wrap', color}}
            placeholder={placeholder || "Type your text here..."}
        />
      </div>
    )
}

const TextSettings = () => {
    let { actions: {setProp}, fontSize, textAlign } = useNode((node) => ({
      fontSize: node.data.props.fontSize,
      textAlign: node.data.props.textAlign,
    }));
    
    console.log(textAlign)

    return (
      <>
          <p>Alignment</p><hr/>
          <div className="nav-responsive-holder">
            <button onClick={() => setProp(props => props.textAlign = "left")}><img src="./assets/align_left.png" alt="mobile"></img></button>
            <button onClick={() => setProp(props => props.textAlign = "center")}><img src="./assets/align_center.png" alt="mobile"></img></button>
            <button onClick={() => setProp(props => props.textAlign = "right")}><img src="./assets/align_right.png" alt="mobile"></img></button>
          </div>
          <p>Type</p><hr/>
          <div className="nav-responsive-holder">
            <button onClick={() => setProp(props => props.tagName = "h1")}><img src="./assets/text_h1.png" alt="mobile"></img></button>
            <button onClick={() => setProp(props => props.tagName = "h2")}><img src="./assets/text_h2.png" alt="mobile"></img></button>
            <button onClick={() => setProp(props => props.tagName = "p")}><img src="./assets/text_p.png" alt="mobile"></img></button>
            <button onClick={() => setProp(props => props.tagName = "div")}><img src="./assets/text.png" alt="mobile"></img></button>
          </div>
      </>
    )
}

Text.craft = {
    props: {
        text: "",
        fontSize: 20,
        textAlign: "left",
    },
    rules: {
      canDrag: (node) => node.data.props.text != "Drag"
    },
    related: {
        settings: TextSettings
    }  
}