import React from 'react';
import { useNode, Element } from "@craftjs/core";

function Video({size, align, source}) {
    const { connectors: {connect, drag}, selected, dragged, actions: {setProp} } = useNode((state) => ({
        selected: state.events.selected,
        dragged: state.events.dragged
    }));
    console.log(source + 't=#0.5')
    return (
        <div className="bvp-video-container" style={{textAlign: align}} ref={ref => connect(drag(ref))}>
            <video style={{width: size + 'px', maxWidth: '100%'}} controls controlsList="nodownload noplaybackrate" preload="metadata" disablePictureInPicture>
                <source src={source + '#t=0.5'} type="video/mp4"/>
            </video>
            <div></div>
        </div>
    );
}

export const VideoSettings = () => {
    let { actions: {setProp}, size, align } = useNode((node) => ({
        size: node.data.props.size,
        align: node.data.props.align,
    }));

    return (
      <>
          <p>Size</p><hr/>
          <div className="nav-responsive-holder">
            <button style={{color: 'white'}} onClick={() => setProp(props => props.size = "200")}>
                <img src="./assets/size_small.png" alt="mobile"></img>
            </button>
            <button style={{color: 'white'}} onClick={() => setProp(props => props.size = "400")}>
                <img src="./assets/size_medium.png" alt="mobile"></img>
            </button>
            <button style={{color: 'white'}} onClick={() => setProp(props => props.size = "800")}>
                <img src="./assets/size_large.png" alt="mobile"></img>
            </button>
          </div>

          <p>Align</p><hr/>
          <div className="nav-responsive-holder">
            <button style={{color: 'white'}} onClick={() => setProp(props => props.align = "left")}>
                <img src="./assets/align_left.png" alt="mobile"></img>
            </button>
            <button style={{color: 'white'}} onClick={() => setProp(props => props.align = "center")}>
                <img src="./assets/align_center.png" alt="mobile"></img>
            </button>
            <button style={{color: 'white'}} onClick={() => setProp(props => props.align = "right")}>
                <img src="./assets/align_right.png" alt="mobile"></img>
            </button>
          </div>
      </>
    )
}


Video.craft = {
    props: {
        size: "400",
        align: "center",
    },
    rules: {
      canDrag: (node) => node.data.props.text !== "Drag"
    },
    related: {
        settings: VideoSettings
    }  
}

export default Video;
