import React from 'react';

function MenuCard(props) {
    const { image, text, refer, disabled } = props
    console.log(props)
    return (
        <button className={"mc-item-holder " + (disabled ? "disabled-menu-item" : "")} ref={refer}>
            <img src={image} alt={text}/>
            <p>{text}</p>
        </button>
    );
}

export default MenuCard;
