import React from "react";
import { useNode } from "@craftjs/core";

const LineBreak = ({margin}) => {
    // Node is selectable and editable
    const { connectors: {connect, drag}, selected, dragged, actions: {setProp} } = useNode((state) => ({
        selected: state.events.selected,
        dragged: state.events.dragged
      }));

    return (
        <div style={{height: margin + 'px', minHeight: margin + 'px'}} ref={ref => connect(drag(ref))}>
        </div>
    )
}

const LineBreakSettings = () => {
    // Update Node with Settings from defined props
    let { actions: {setProp}, margin, textAlign } = useNode((node) => ({
        margin: node.data.props.margin,
    }));
    
    console.log(textAlign)

    return (
      <>
          <p>Distance</p><hr/>
          <div className="nav-responsive-holder">
            <button onClick={() => {
                if(margin === 5) return;
                setProp(props => props.margin -= 5)
            }}>
                <img src="./assets/option_remove.png" alt="mobile"></img>
            </button>
            <p>{margin}</p>
            <button onClick={() => setProp(props => props.margin += 5)}><img src="./assets/option_add.png" alt="mobile"></img></button>
          </div>
      </>
    )
}

// Craft default settings
LineBreak.craft = {
    props: {
        margin: 5,
    },
    rules: {
      canDrag: (node) => node.data.props.text != "Drag"
    },
    related: {
        settings: LineBreakSettings
    }  
}

export default LineBreak