import React from "react";
import { useNode } from "@craftjs/core";
import {Text} from '../Text/Text'
import ContentEditable from 'react-contenteditable'

const FlipCard = ({text, textBack, image, disableAnimation, flip}) => {
        // Node is selectable and editable
        const { connectors: {connect, drag}, selected, dragged, actions: {setProp} } = useNode((state) => ({
            selected: state.events.selected,
            dragged: state.events.dragged
        }));

        console.log(disableAnimation, flip)
        return (
            <div className={"flip-card " + (!disableAnimation ? "flip-card-action " : "") + (flip ? "flip-card-flip" : "")} ref={ref => connect(drag(ref))}>
                <div className="flip-card-inner">
                    <div className="flip-card-front">
                        <img src={image} alt="some"/>
                        <Text tagName="p" text={text} textAlign="left"/> 
                    </div>
                    <div className="flip-card-back">
                        <ContentEditable 
                            html={""} 
                            onKeyDown={(event) => {
                                if (event.key === 'Enter') {
                                    document.execCommand('insertLineBreak')
                                    event.preventDefault()
                                }
                            }}
                            tagName={"p"}
                            style={{whiteSpace: 'pre-wrap', color: '#fff', textAlign: 'left'}}
                            placeholder="type your text here ... "
                        />
                    </div>
                </div>
            </div>
        )
}

const FlipCardSettings = () => {
    // Update Node with Settings from defined props
    let { actions: {setProp}, fontSize, textAlign } = useNode((node) => ({
      attr1: node.data.props.attr1,
    }));
    
    console.log(textAlign)

    return (
      <>
          <p>Animation</p><hr/>
          <div className="nav-responsive-holder">
            <button style={{color: 'white'}} onClick={() => setProp(props => props.disableAnimation = !props.disableAnimation)}>Disable Animation</button>
            <button style={{color: 'white'}} onClick={() => setProp(props => props.flip = !props.flip)}>Flip</button>
          </div>
      </>
    )
}

// Craft default settings
FlipCard.craft = {
    props: {
        disableAnimation: false,
        flip: false,
    },
    rules: {
      canDrag: (node) => node.data.props.text != "Drag"
    },
    related: {
        settings: FlipCardSettings
    }  
}

export default FlipCard