import React from "react";
import { Element, useNode } from "@craftjs/core";
import GridQuarter from "./GridQuarter";
import GridFull from "./GridFull";
import EmptyContainer from "../EmptyContainer";
import GridTemplate from "./GridTemplate";

const GridWrapper = ({children, boxesCount}) => {
    // Node is selectable and editable
    const { connectors: {connect, drag}, selected, dragged, actions: {setProp} } = useNode((state) => ({
        dragged: state.events.dragged
      }));

    return (
        <div className="grids-itemwrapper-holder" ref={ref => connect(drag(ref))}>
            {
                new Array(boxesCount).fill("").map((item, index) => <Element key={"grid-wrapper" + index} id={"grid-wrapper" + index} is={GridTemplate} padding={20} canvas />)
            }
            {children}
        </div>
    )
}

const GridWrapperSettings = () => {
    // Update Node with Settings from defined props
    let { actions: {setProp}, boxesCount, textAlign } = useNode((node) => ({
        boxesCount: node.data.props.boxesCount,
    }));
    
    console.log(boxesCount)

    return (
      <>
          <p>Items Amount</p><hr/>
          <div className="nav-responsive-holder">
            <button onClick={() => {
                if(boxesCount === 1) return;
                setProp(props => props.boxesCount -= 1)
            }}>
                <img src="./assets/option_remove.png" alt="mobile"></img>
            </button>
            <p>{boxesCount}</p>
            <button onClick={() => setProp(props => props.boxesCount += 1)}><img src="./assets/option_add.png" alt="mobile"></img></button>
          </div>
      </>
    )
}

// Craft default settings
GridWrapper.craft = {
    props: {
        attr1: "",
        boxesCount: 3,
    },
    rules: {
      canDrag: (node) => node.data.props.text != "Drag"
    },
    related: {
        settings: GridWrapperSettings
    }  
}

export default GridWrapper