import React from "react";
import {Paper, FormControl, FormLabel, Slider} from "@material-ui/core";
import { useNode } from "@craftjs/core";

export const Container = ({background, padding = 0, children, style}) => {
    const { connectors: {connect, drag} } = useNode();
    // , overflowY: 'scroll', scrollbarWidth: 'none', maxHeight: '97vh'
    return (
        <Paper ref={ref=> connect(drag(ref))} style={{...style, ...({background, padding: `${padding}px`})}}>
          {children}
        </Paper>
    )
}

export const ContainerSettings = () => {
    const { background, padding, actions: {setProp} } = useNode(node => ({
      background: node.data.props.background,
      padding: node.data.props.padding
    }));
    return (
      <div>
        <FormControl fullWidth={true} margin="normal" component="fieldset">
          <FormLabel component="legend">Background</FormLabel>
        </FormControl>
        <FormControl fullWidth={true} margin="normal" component="fieldset">
          <FormLabel component="legend">Padding</FormLabel>
          <Slider defaultValue={padding} onChange={(_, value) => setProp(props => props.padding = value)} />
        </FormControl>
      </div>
    )
}

export const ContainerDefaultProps = {
    background : "#ffffff",
    padding: 3
};
  
Container.craft = {
    props: ContainerDefaultProps,
    related: {
        settings: ContainerSettings
    }
}