import React, { useCallback, useState } from "react";
import {Text} from "../Text/Text";
import {Button} from "../Button";
import { Element, useNode } from "@craftjs/core";
import {useDropzone} from 'react-dropzone';
import { Container, ContainerSettings, ContainerDefaultProps }  from "../Container";
import Image from "./Image";
import Video from "./Video";
import Audio from "./Audio";
import EmptyContainer from "../EmptyContainer";

const fileAcceptBuilder = {
    'image': 'image/*',
    'video': 'video/*',
    'audio': 'audio/*',
}

function UploadCard({fileType}) {
    const [ file, setFile ] = useState(null)
    
    const onDrop = useCallback(acceptedFiles => {
        console.log(acceptedFiles)
        if(acceptedFiles.length !== 0){
            setFile(URL.createObjectURL(acceptedFiles[0]))
        }
        else {
            console.log('empty');
        }

    }, [])

    const {getRootProps, getInputProps, open, acceptedFiles} = useDropzone({
        noClick: true,
        noKeyboard: true,
        multiple: false,
        accept: fileAcceptBuilder[fileType],
        onDrop
    });
    
    const files = acceptedFiles.map(file => (
        <i id="exlude-border" key={file.path}>
            {file.path} - {file.size} bytes
        </i>
    ));

    return (
        <>
        {
            file ? (
                <Element is={EmptyContainer} id="image-element" canvas>
                    {
                        fileType === 'image' ? (<Image source={file}></Image>) :
                        fileType === 'video' ? (<Video source={file}></Video>) : 
                        (<Audio source={file}></Audio>)
                    }
                </Element>
            ) : (
                <div className="uc-holder">
                    <div id="exlude-border" className="form-group-files">
                        <div id="dropzone" {...getRootProps({className: 'dropzone'})}>
                        <input {...getInputProps()} />
                        <p id="exlude-border">Drag 'n' drop your {fileType} here</p>
                        <button type="button" className="btn-open-file-selector" onClick={open}>
                            Open File Dialog
                        </button>
                        </div>
                        {/* 
                        <aside>
                        <p id="exlude-border">{files}</p>
                        </aside>
                        <p id="exlude-border" className="dashed-line-break"><b>or</b></p>
                        <label>Select an Url: </label>
                        <input type="text" className="input-form-control" multiple={false}/>
                        <p id="exlude-border" className="dashed-line-break"><b>or</b></p>
                        <label>Embed a file: </label>
                        <input type="text" className="input-form-control" multiple={false}/> 
                        */}
                    </div>
                </div>
            )
        }
        </>
    )
}


UploadCard.craft = {
    props: ContainerDefaultProps,
    related: {
        settings: ContainerSettings
    }
}

export default UploadCard;
