import React from 'react';
import { Box, Chip, Grid, Typography, Button as MaterialButton, FormControl, FormLabel, Slider } from "@material-ui/core";
import { useEditor } from "@craftjs/core";

export const SettingsPanel = () => {  
    const { actions, selected } = useEditor((state, query) => {
        const currentNodeId = state.events.selected;
        let selected;
    
        if ( currentNodeId.size ) {
            const itemId = Array.from(currentNodeId)[0]
            selected = {
              id: itemId,
              name: state.nodes[itemId].data.name || "sdadsaads",
              settings: state.nodes[itemId].related && state.nodes[itemId].related.settings,
              isDeletable: query.node(itemId).isDeletable()
            };
        }
    
        return {
          selected
        }
      });
    
      return selected ? (
        <div className="settings-panel-container">
            <div className="sp-selected">{selected.name}</div>
            <br/>
            { 
                selected.settings && React.createElement(selected.settings)
            }
            <br/><hr/>
            {
            selected.isDeletable ? (
                    <MaterialButton
                    variant="contained"
                    color="primary"
                    onClick={() => {
                        actions.delete(selected.id);
                    }}
                    >
                    Delete
                    </MaterialButton>
                ) : null
            }
        </div>    
      ) : null
}