import React from "react";
import { useNode } from "@craftjs/core";

const HorizontalLine = ({height, color}) => {
    // Node is selectable and editable
    const { connectors: {connect, drag}, selected, dragged, actions: {setProp} } = useNode((state) => ({
        selected: state.events.selected,
        dragged: state.events.dragged
      }));

    return (
        <hr style={{border: height + 'px solid ' + color}} ref={ref => connect(drag(ref))}>
        </hr>
    )
}

const HorizontalLineSettings = () => {
    // Update Node with Settings from defined props
    let { actions: {setProp}, height, color } = useNode((node) => ({
        height: node.data.props.height,
        color: node.data.props.color,
    }));
    
    console.log(color)

    return (
      <>
          <p>Size</p><hr/>
          <div className="nav-responsive-holder">
            <button onClick={() => {
                if(height === 1) return;
                setProp(props => props.height -= 1)
            }}>
                <img src="./assets/option_remove.png" alt="mobile"></img>
            </button>
            <p>{height}</p>
            <button onClick={() => setProp(props => props.height += 1)}>
                <img src="./assets/option_add.png" alt="mobile"></img>
            </button>
          </div>
          <p>Color</p><hr/>
          <div className="nav-responsive-holder">
            <button onClick={() => {
                setProp(props => props.color = '#cfcfcf')
            }}>
                <div style={{backgroundColor: '#cfcfcf', borderRadius: '50%', width: '20px', height: '20px'}}></div>
            </button>
            <button onClick={() => setProp(props => props.color = '#04bf7a')}>
                <div style={{backgroundColor: '#04bf7a', borderRadius: '50%', width: '20px', height: '20px'}}></div>
            </button>
          </div>
      </>
    )
}

// Craft default settings
HorizontalLine.craft = {
    props: {
        height: 1,
        color: '#cfcfcf',
    },
    rules: {
      canDrag: (node) => node.data.props.text != "Drag"
    },
    related: {
        settings: HorizontalLineSettings
    }  
}

export default HorizontalLine