import React from "react";
import { useNode } from "@craftjs/core";

const GridQuarter = ({children}) => {
    // Node is selectable and editable
    const { connectors: {connect, drag}, selected, dragged, actions: {setProp} } = useNode((state) => ({
        dragged: state.events.dragged
      }));

    return (
        <div className="grid-quarter-container" ref={ref => connect(drag(ref))}>
            {children}
        </div>
    )
}

const GridFullSettings = () => {
    // Update Node with Settings from defined props
    let { actions: {setProp}, fontSize, textAlign } = useNode((node) => ({
      attr1: node.data.props.attr1,
    }));
    
    console.log(textAlign)

    return (
      <>
          <p>Alignment</p><hr/>
          <div className="nav-responsive-holder">
            <button onClick={() => setProp(props => props.attr1 = "")}><img src="./assets/align_left.png" alt="mobile"></img></button>
          </div>
      </>
    )
}

// Craft default settings
GridQuarter.craft = {
    props: {
        attr1: "",
    },
    rules: {
      canDrag: (node) => node.data.props.text != "Drag"
    },
    related: {
        settings: GridFullSettings
    }  
}

export default GridQuarter
